// Libraries
import React, {Component} from 'react';
import axios from 'axios';
import {connect} from 'react-redux';
import {Button} from 'antd';
import {Modal, ModalHeader, ModalBody, ModalFooter, Form, FormGroup, Label, Input} from 'reactstrap';
import {toast, ToastContainer} from 'react-toastify';

// Actions
import {GET_ROOMS} from '../../../actions/RoomAction';

// Components
import Spin from '../../admin/Spin';
import RoomRow from './RoomRow';

class RoomTable extends Component {

    state = {
        rooms : [],
        isOpenModal: false,
        isLoading: false,
        nameRoom: '',
        maxPeople: null,
        floor : null,
        square: null,
        price: null,
        description: '',
        status: ''
    }

    toggle = () => {
        this.setState({
            isOpenModal: !this.state.isOpenModal
        });
    }

    onChange(e) {
        this.setState({
            [e.target.name] : e.target.value
        });
    }

    componentDidUpdate(prevProps) {
        if (this.props.idBlock !== prevProps.idBlock) {
            this.getRooms();
        }
    }

    getRooms = async () =>{
        this.setState({
            isLoading: true
        });

        let token = localStorage.getItem('token');
        const getRooms = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/room/get-rooms?token=${token || ''}&idBlock=${this.props.idBlock}`,
            method: 'GET'
        });

        if (getRooms) {
            if (getRooms.data && getRooms.data.data) {
                const {rooms = []} = getRooms.data.data;

                this.setState({
                    rooms
                });
            } 
        }

        this.setState({isLoading: false});
    }

    onAdding = async () =>{
        this.setState({
            isLoading: true
        });

        let token = localStorage.getItem('token');

        const result = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/room/create?token=${token || ''}`,
            method: 'POST',
            data: {
                nameRoom: this.state.nameRoom,
                maxPeople: +this.state.maxPeople,
                floor: +this.state.floor,
                square: +this.state.square,
                price: +this.state.price,
                description: this.state.description,
                idBlock: this.props.idBlock,
                status: 0
            }
        });

        this.setState({
            isLoading: false
        });
        if (result) {
            if (result.data && result.data.data) {
                toast.success('Thm phng thnh cng !');

                this.setState({
                    isOpenModal: !this.state.isOpenModal
                });
                this.getRooms();
            } else {

                toast.error('Thm phng tht bi !');
            }
        } 
    }

    addBlockModal = () =>{
        this.setState({
            isOpenModal: true,
            isLoading: false,
            nameRoom: '',
            maxPeople: null,
            floor : null,
            square: null,
            price: null,
            description: '',
            status: ''
        });
    }

    callBackBlockRow = (value) =>{
        if (value) {
            this.getRooms();
        }
    }

    callBackRoomRow = value =>{
        if (value) {
            this.getRooms();
        }
    }

    renderRooms = () => {
        const {rooms = []} = this.state;

        if (Array.isArray(rooms) && rooms.length > 0) {
            return rooms.map(room => {
                return <React.Fragment key={room.id}>
                    <RoomRow 
                        room={room}
                        callback = {this.callBackRoomRow}
                    />
                </React.Fragment>;
            });
        }

    }

    render() {
        const {idBlock} = this.props;

        return (
            <div style={{position: 'relative'}}>   
                {this.state.isLoading ?  <Spin /> : null}       
                <div className="card shadow mb-4">
                    <div className="card-header py-3 row" style = {{display : 'flex', alignItems : 'center', justifyContent : 'space-between'}}>
                        <div className="sm-6">
                            <h6 className="m-0 font-weight-bold text-primary">QUN L DANH SCH PHNG</h6>                          
                        </div>
                        <div>
                            <Button 
                                outline color= {idBlock === null ? 'secondary' : 'primary' }
                                onClick = {this.addBlockModal}
                                disabled={idBlock === null ? true : false}
                                title = {idBlock === null ? 'Phi c t nht 1 khu tr  thc hin thm phng' : ''}
                            >Thm phng</Button>{' '}                                 
                        </div>
                                   
                    </div>
                    
                    <div className="card-body">
                        <div className="table-responsive">
                            <table className="table table-bordered" id="dataTable" width="100%" cellSpacing={0}>
                                <thead>
                                    <tr>
                                        <th>Tn phng</th>
                                        <th>Tng</th>
                                        <th>Din tch</th>
                                        <th>S ngi ti a</th>
                                        <th>Gi</th>
                                        <th>M t</th>
                                        <th>Trng thi</th>
                                        <tr />
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.renderRooms()}                      
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>       
                <Modal isOpen={this.state.isOpenModal} toggle={this.toggle}>
                    {this.state.isLoading ?  <Spin /> : null}       
                    <ModalHeader toggle={this.toggle}>THM PHNG</ModalHeader>
                    <ModalBody>
                        <Form>
                            <FormGroup>
                                <Label for="nameRoom">Tn phng (*)</Label>
                                <Input type="text" name="nameRoom" id="nameRoom" onChange={(e) => this.onChange(e)} value={this.state.nameRoom} />
                                <Label for="floor">Tng (*) </Label>
                                <Input type="number" name="floor" id="floor" onChange={(e) => this.onChange(e)} value={this.state.floor} />
                            </FormGroup>
                            <FormGroup>
                                <Label for="maxPeople">S ngi ti da (*) </Label>
                                <Input type="number" name="maxPeople" id="maxPeople" onChange={(e) => this.onChange(e)} value={this.state.maxPeople} />
                                <Label for="square">Din tch (*) </Label>
                                <Input type="number" name="square" id="square" onChange={(e) => this.onChange(e)} value={this.state.square} />
                                <Label for="price">Gi (*)</Label>
                                <Input type="number" name="price" id="price" onChange={(e) => this.onChange(e)} value={this.state.price} />
                            </FormGroup>
                            <FormGroup>
                                <Label for="description">M t</Label>
                                <Input type="textarea" name="description" id="description" onChange={(e) => this.onChange(e)} value={this.state.description} />
                            </FormGroup>
                            <FormGroup>
                                <Label for="exampleFile">File</Label>
                                <Input type="file" name="file" id="exampleFile" />
                            </FormGroup>
                        </Form>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.onAdding}>Thm</Button>{' '}
                        <Button color="secondary" onClick={this.toggle}>Hy</Button>
                    </ModalFooter>
                </Modal>
                <ToastContainer />
            </div>
        );
    }
}

const mapDispatchToProps =  {
    GET_ROOMS
};

const mapStateToProps = state => {
    return {
        roomsState: state.RoomReducer.rooms
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(RoomTable);