// Libraries
import React, {Component} from 'react';
import axios from 'axios';
import {connect} from 'react-redux';
import {Modal, ModalHeader, ModalBody, Input} from 'reactstrap';
import {toast, ToastContainer} from 'react-toastify';
import numeral from 'numeral';
import {Form, Button, Checkbox, Row, Col, Divider, Select, DatePicker} from 'antd';
import moment from 'moment';

// Actions
import {GET_ROOMS} from '../../../actions/RoomAction';

// Components
import Spin from '../../admin/Spin';
import BillRow from './BillRow';

const {Option} = Select;

const layout = {
    labelCol: {
        span: 24
    },
    wrapperCol: {
        span: 24
    }
};

class BillTable extends Component {
    formRef = React.createRef();

    state = {
        bills: [],
        rooms: [],
        services: [],
        isOpenModal: false,
        isLoading: false,
        isLoadingForm: false,
        isCheckedOut: false,
        totalPrice: null
    }

    toggle = () => {
        this.setState({
            isOpenModal: !this.state.isOpenModal
        });
    }

    onChange(e) {
        this.setState({
            [e.target.name] : e.target.value
        });
    }

    componentDidUpdate(prevProps) {
        if (this.props.idBlock !== prevProps.idBlock) {
            this.getBills();
            this.getRooms();
            this.getServices();
        }
    }

    getBills = async () =>{
        this.setState({
            isLoadingForm: true
        });

        let token = localStorage.getItem('token');
        const getBills = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/bill/get-bills/${this.props.idBlock}?token=${token || ''}`,
            method: 'GET'
        });

        if (getBills) {
            if (getBills.data && getBills.data.data) {
                const {bills = []} = getBills.data.data;

                this.setState({
                    bills
                });
            } 
        }

        this.setState({isLoadingForm: false});
    }

    getServices = async () =>{
        this.setState({
            isLoadingForm: true
        });

        let token = localStorage.getItem('token');
        const getServices = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/service/get-services?token=${token || ''}&idBlock=${this.props.idBlock}`,
            method: 'GET'
        });

        if (getServices) {
            if (getServices.data && getServices.data.data) {
                const {services = []} = getServices.data.data;

                this.setState({
                    services
                });
            } 
        }

        this.setState({isLoadingForm: false});
    }

    addServiceModal = () =>{
        this.setState({
            isOpenModal: true,
            isLoading: false
        });
    }

    callBackBillRow = (value) =>{
        if (value) {
            this.getBills();
        }
    }

    renderBills = () => {
        const {bills = []} = this.state;

        if (Array.isArray(bills) && bills.length > 0) {
            return bills.map(bill => {
                return <React.Fragment key={bill.id}>
                    <BillRow 
                        bill={bill}
                        callback = {this.callBackBillRow}
                        block= {this.props.block}
                        rooms = {this.state.rooms}
                        services = {this.state.services}
                    />
                </React.Fragment>;
            });
        }
    }

    onFinish = async (value) => {

        let token = localStorage.getItem('token');

        this.setState({
            isLoading: true
        });

        const {services} = this.state;

        let billDetail = [];

        services.filter(service => {
            if (service.number && service.number > 0)
            {
                billDetail = [...billDetail,{
                    idService: +service.id,
                    amount: +service.number,
                    totalPrice: +service.total
                } ]; 
            }
        });

        if (billDetail.length === 0 )
        {
            toast.error('Ha n phi c t nht 1 dch v !');
            this.setState({isLoading: false});
            return;
        }

        const create = await axios({
            method: 'POST',
            url: `https://api-gateway.vmotel.me/go-lang/bill/create?token=${token || ''}`,
            data: {
                idRoom: +value.room,
                dateCheckOut: moment(value.dateCheckOut).format('YYYY-MM-DD'),
                totalPrice: +this.state.totalPrice,
                isCheckedOut: this.state.isCheckedOut === true ? 1 : 0,
                billDetail
            }
        });

        if (create) {
            if (create.data && create.data.data) {
                toast.success('Thm ha n thnh cng !');
                
                this.setState({
                    isOpenModal: !this.state.isOpenModal,
                    optionCustomers: []
                });

                this.getBills();
            } else {
                toast.error('Thm ha n tht bi !');
            }
        }

        this.setState({isLoading: false});
    }

    getRooms = async () =>{
        this.setState({
            isLoading: true
        });

        let token = localStorage.getItem('token');
        const getRooms = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/room/get-rooms?token=${token || ''}&idBlock=${this.props.idBlock}`,
            method: 'GET'
        });

        if (getRooms) {
            if (getRooms.data && getRooms.data.data) {
                const {rooms = []} = getRooms.data.data;

                this.setState({
                    rooms
                });
            } 
        }

        this.setState({isLoading: false});
    }

    renderSelectRoom = () =>{
        const {rooms} = this.state;

        if (Array.isArray(rooms) && rooms.length > 0) {
            return rooms.map(room => {
                return <React.Fragment key={room.id}>
                    <option onClick={() => this.onClickBlock(room.id)}>{room.nameRoom}</option>
                </React.Fragment>;
            });
        }
    }

    onChangeSelected = () => {

    }

    onChangeIsCheckout = () =>{
        this.setState({
            isCheckedOut: !this.state.isCheckedOut
        });
    }

    onChangeNum = (event, price, index) => {
        if (event.target.value !== '') {
            let services = [...this.state.services];
            let newService = this.state.services[index];

            newService = {
                ...newService,
                number: +event.target.value < 0 ? 0 : +event.target.value,
                total: +event.target.value < 0 ? 0 * price : event.target.value * price
            };
            services[index] = newService;

            this.setState({
                services
            });
        
            // console.log(services);
            if (Array.isArray(services) && services.length > 0) {
                let totalPrice = 0;

                services.map(service => {
                    if (service.total && service.number > 0)
                    {totalPrice += service.total}
                    else if (service.number > 0)
                    {totalPrice += service.number ? service.price * service.number : service.price}
                });
                
                this.setState({
                    totalPrice
                });
            }
        }
    }

    renderServices = () => {
        const {services} = this.state;

        if (Array.isArray(services) && services.length > 0) {
            return services.map((service, index) => {
                let {total = 0} = service;
                let {number = 0} = service;
                
                return <React.Fragment key={service.id}>
                    <tr>
                        <td>{service.nameService}</td>
                        <td>{numeral(service.price).format('0,0')}</td>
                        <td><Input type= 'number' defaultValue={0} value={number} name='amount' onChange = {(e) => this.onChangeNum(e, service.price, index)} /></td>
                        <td>{numeral(total).format('0,0')}</td>
                    </tr>
                </React.Fragment>;
            });
        }
    }

    render() {
        const {idBlock} = this.props;
        const {bills} = this.state;

        return (
            <div style={{position: 'relative'}}>   
                {this.state.isLoadingForm ?  <Spin /> : null}       
                <div className="card shadow mb-4">
                    <div className="card-header py-3 row" style = {{display : 'flex', alignItems : 'center', justifyContent : 'space-between'}}>
                        <div className="sm-6">
                            <h6 className="m-0 font-weight-bold text-primary">QUN L DANH SCH HA N</h6>                          
                        </div>
                        <div>
                            <Button 
                                outline color= {idBlock === null ? 'secondary' : 'primary' }
                                disabled={idBlock === null ? true : false}
                                title = {idBlock === null ? 'Phi c t nht 1 khu tr  thc hin thm ha n' : ''}
                                onClick = {this.addServiceModal}
                            >Thm ha n</Button>{' '}                                 
                        </div>
                                   
                    </div>
                    
                    <div className="card-body">
                        <div className="table-responsive">
                            <table className="table table-bordered" id="dataTable" width="100%" cellSpacing={0}>
                                <thead>
                                    <tr>
                                        <th>Tn phng</th>
                                        <th>Ngy to</th>
                                        <th>Tng gi</th>
                                        <th>Trng thi</th>

                                        <tr />
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.renderBills()}                      
                                </tbody>
                                
                            </table>
                            {bills.length == 0 ? <div style={{display: 'flex', justifyContent: 'center'}}>
                                                Hin ti cha c d liu
                            </div> : ''}
                        </div>
                    </div>
                </div>       
                <Modal isOpen={this.state.isOpenModal} toggle={this.toggle}>
                    {this.state.isLoading ?  <Spin /> : null}       
                    <ModalHeader toggle={this.toggle}>THM HA N</ModalHeader>
                    <ModalBody>
                        <Form {...layout} ref={this.formRef} onFinish={this.onFinish} initialValues = {{
                            dateCheckOut : moment(Date.now())
                        }}>
                            <Form.Item
                                label="Khu tr/cn h"
                            >
                                <strong>{this.props.block && this.props.block.nameBlock}</strong>
                            </Form.Item>
                            <Divider />
                            <Row>
                                <Col xs={12}>
                                    <Form.Item
                                        label="Chon phong"
                                        name="room"
                                        rules={[{required: true, message: 'Hay chon phong'}]}
                                    >
                                        <Select
                                            placeholder="Chon phong"
                                            style={{width: 200}}
                                        >
                                            {this.state.rooms.map(room => (
                                                <Option key={room.id}>{room.nameRoom}</Option>
                                            ))}
                                        </Select>
                                    </Form.Item>
                                </Col>
                                <Col xs={12}>
                                    <Form.Item 
                                        name="dateCheckOut" 
                                        label="Ngy to" 
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Hy chn ngy to ha n'
                                            }
                                        ]}
                                    >
                                        <DatePicker style={{width: '200px'}} format= 'DD-MM-YYYY' />
                                    </Form.Item>
                                </Col>
                            </Row>
                                                      
                            <Form.Item
                                label = 'Danh sch dch v'
                            >
                                <table className="table table-bordered" id="dataTable" width="100%" cellSpacing={0}>
                                    <thead>
                                        <tr>
                                            <th>Tn dch v</th>
                                            <th>n gi</th>
                                            <th>S lng</th>
                                            <th>Tng gi</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.renderServices()}                      
                                    </tbody>
                                
                                </table>
                            </Form.Item>

                            <Divider />

                            <Row>
                                <Col xs={12}>
                                    <Form.Item
                                        label="Tng tin"
                                    >
                                        <strong>{numeral(this.state.totalPrice).format('0,0')}  vn</strong>
                                    </Form.Item>
                                </Col>
                                <Col xs={12}>
                                    <Form.Item 
                                        name = 'isCheckedOut'
                                        label="Trng thi"
                                    >
                                        <Checkbox checked={this.state.isCheckedOut} onChange={this.onChangeIsCheckout}>Thanh ton</Checkbox>
                                    </Form.Item>
                                </Col>
                            </Row>
                            
                            <Form.Item
                                wrapperCol={{
                                    md: {span: 8, offset: 16}
                                }}
                            >
                                <Button htmlType='submit' type='primary' >Thm</Button>{' '}
                                <Button  onClick={this.toggle}>Hy</Button>
                            </Form.Item>
                        </Form>
                    </ModalBody>
                </Modal>
                <ToastContainer />
            </div>
        
        );
    }
}

const mapDispatchToProps =  {
    GET_ROOMS
};

const mapStateToProps = state => {
    return {
        roomsState: state.RoomReducer.rooms
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(BillTable);