import React, {Component} from 'react';
import {Button, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';
import {Form, FormGroup, Label, Input} from 'reactstrap';
import axios from 'axios';
import {toast, ToastContainer} from 'react-toastify';
import Spin from '../../admin/Spin';
import {Tag} from 'antd'; 

export default class BlockRow extends Component {
    state = {
        isOpenModal: false,
        isOpenDeleteModal: false,
        nameBlock: '',
        address: '',
        description : '',
        isLoading: false
    }

    toggle = () => {
        this.setState({
            isOpenModal: !this.state.isOpenModal
        });
    }

    toggleDelete = () =>{
        this.setState({
            isOpenDeleteModal: !this.state.isOpenDeleteModal
        });
    }

    updateData= async () =>{
        this.setState({
            isLoading: true
        });

        let token = localStorage.getItem('token');
        const result = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/block/update/${this.props.block.id}?token=${token || ''}`,
            method: 'PUT',
            data: {
                id: this.props.block.id,
                nameBlock : this.state.nameBlock,
                address : this.state.address,
                description : this.state.description,
                idOwner : this.props.block.idOwner
            }
        });

        this.setState({
            isLoading: false
        });

        if (result) {
            if (result.data && result.data.data) {
                toast.success('Cp nht thng tin thnh cng !');
                
                this.props.callback(true);
            } else {

                toast.error('Cp nht thng tin tht bi !');
            }
        } 

        this.setState({
            isOpenModal: !this.state.isOpenModal
        });
    }

    onDelete = async () =>{

        this.setState({
            isOpenDeleteModal: true
        });

    }

    onDeleting = async () =>{
        this.setState({
            isLoading: true
        });

        let token = localStorage.getItem('token');
        const result = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/block/delete/${this.props.block.id}?token=${token || ''}`,
            method: 'DELETE'
        });

        this.setState({
            isLoading: false
        });

        if (result) {
            if (result.data && result.data.data) {
                toast.success('Xa khu tr thnh cng !');
               
                this.props.callback(true);

                this.setState({
                    isOpenDeleteModal: !this.state.isOpenDeleteModal
                });
            } else {

                toast.error('Xa khu tr tht bi !');
            }
        } 
    }

    onEditing = () =>{
        this.updateData();      
    }
    
    onClickEdit = () => {
        this.setState({
            isOpenModal: true,
            nameBlock : this.props.block.nameBlock,
            address: this.props.block.address,
            description : this.props.block.description
        });
    }

    onChange(e) {
        this.setState({
            [e.target.name] : e.target.value
        });
    }

    render() {
        
        return (
            <React.Fragment>
                <tr>
                    <td><Tag color="#f50">{this.props.block.nameBlock}</Tag></td>
                    <td>{this.props.block.address}</td>
                    <td>{this.props.block.description}</td>
                    <td>
                        <button onClick={this.onClickEdit} className="btn btn-warning btn-circle mr-3" title="Chnh sa">
                            <i className="fa fa-pencil-square" aria-hidden="true" />
                        </button>
                        <button onClick={this.onDelete} className="btn btn-danger btn-circle" title="Xa">
                            <i className="fa fa-trash" aria-hidden="true" />
                        </button>
                    </td>
                </tr>
                <Modal isOpen={this.state.isOpenModal} toggle={this.toggle}>
                    {this.state.isLoading ?  <Spin /> : null}  
                    <ModalHeader toggle={this.toggle}>CHNH SA THNG TIN KHU TR</ModalHeader>
                    <ModalBody>
                        <Form>
                            <FormGroup>
                                <Label for="exampleNameBlock">Tn khu tr</Label>
                                <Input type="email" name="nameBlock" id="exampleNameBlock" onChange={(e) => this.onChange(e)} value={this.state.nameBlock} />
                            </FormGroup>
                            <FormGroup>
                                <Label for="exampleAddress">a ch</Label>
                                <Input type="email" name="address" id="exampleAddress" onChange={(e) => this.onChange(e)} value={this.state.address} />
                            </FormGroup>
                            <FormGroup>
                                <Label for="exampleDescription">M t</Label>
                                <Input type="email" name="description" id="exampleDescription" onChange={(e) => this.onChange(e)} value={this.state.description} />
                            </FormGroup>
                        </Form>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.onEditing}>Chnh sa</Button>{' '}
                        <Button color="secondary" onClick={this.toggle}>Hy</Button>
                    </ModalFooter>
                </Modal>
                <Modal isOpen={this.state.isOpenDeleteModal} toggle={this.toggle}>
                    {this.state.isLoading ?  <Spin /> : null}  
                    <ModalHeader toggle={this.toggle}>THNG BO</ModalHeader>
                    <ModalBody>
                        Chc chn mun xa khu tr ?
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.onDeleting}>Xa</Button>{' '}
                        <Button color="secondary" onClick={this.toggleDelete}>Hy</Button>
                    </ModalFooter>
                </Modal>
                <ToastContainer />
                
            </React.Fragment>
        );
    }
}
