import React, {Component} from 'react';
import {Form, Input, Radio , Button, Checkbox, Row, Col, Divider, DatePicker, Tag, message} from 'antd';
import {Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';
import axios from 'axios';
import Spin from '../../admin/Spin';
import {toast, ToastContainer} from 'react-toastify';
import moment from 'moment';

const {TextArea} = Input;

const layout = {
    labelCol: {
        span: 24
    },
    wrapperCol: {
        span: 24
    }
};

const formItemLayout = {
    labelCol: {span: 6},
    wrapperCol: {span: 18}
};

export default class CustomerRow extends Component {
    formRef = React.createRef();

    state = {
        customers: [],
        isOpenModal: false,
        isOpenDeleteModal: false,
        isLoading: false,
        isLoadingForm: false,
        loading: false,
        tempReg : false
    }

    onChangeTempReg = () =>{
        this.setState({
            tempReg: !this.state.tempReg
        });
    }

    toggle = () => {
        this.setState({
            isOpenModal: !this.state.isOpenModal
        });
    }

    onClickEdit = () => {
        this.setState({
            isOpenModal: true,
            tempReg : this.props.customer.tempReg
        });
    }

    toggleDelete = () =>{
        this.setState({
            isOpenDeleteModal: !this.state.isOpenDeleteModal
        });
    }

    onClickDelete = () => {
        this.setState({
            isOpenDeleteModal: true
        });
    }

    onDeleting = async () =>{
        this.setState({
            isLoading: true
        });

        let token = localStorage.getItem('token');
        const result = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/customer/delete/${this.props.customer.id}?token=${token || ''}`,
            method: 'DELETE'
        });

        this.setState({
            isLoading: false
        });

        if (result) {
            if (result.data && result.data.data) {
                toast.success('Xa khach hang thnh cng !');
               
                this.props.callback(true);

                this.setState({
                    isOpenDeleteModal: !this.state.isOpenDeleteModal
                });
            } else {

                toast.error('Xa khach hang tht bi !');
            }
        } 
    }

    onFinish = async (value) => {
        let user = JSON.parse(localStorage.getItem('user'));
        let token = localStorage.getItem('token');

        this.setState({
            isLoading: true
        });

        const result = await axios({
            url: `https://api-gateway.vmotel.me/go-lang/customer/update/${this.props.customer.id}?token=${token || ''}&userId=${user.id}`,
            method: 'PUT',
            data: {
                id : this.props.customer.id,
                fullName : value.fullName,
                phoneNumber : value.phoneNumber,
                dateBirth : moment(value.dateBirth).format('YYYY-MM-DD'),
                email : value.email ? value.email : '',
                job : value.job,
                workPlace: value.workPlace ? value.workPlace : '',
                sex : value.sex,
                tempReg: this.state.tempReg ? 1 : 0,
                note : value.note ? value.note : '',
                avatar: '',
                identifyBack: '',
                identifyFront: ''
            }
        });

        this.setState({
            isLoading: false
        });

        if (result) {
            if (result.data && result.data.data) {
                toast.success('Cp nht khach hang thnh cng !');

                this.setState({
                    isOpenModal: !this.state.isOpenModal
                });
                this.props.callback(true);
            } else {

                toast.error('Cp nht khach hang tht bi !');
            }
        }
    }

    render() {
        const {customer} = this.props;

        return (
            <React.Fragment>
                <tr>
                    <td>{customer.fullName}</td>
                    <td>{customer.phoneNumber}</td>
                    <td>{customer.rooms && customer.rooms.length == 0 ? <Tag color="green">Cha thu</Tag> : <Tag color="orange"> thu</Tag>}</td>
                    <td>{customer.rooms ? customer.rooms[0].roomName : ''}</td>
                    <td>
                        <button onClick={this.onClickEdit} className="btn btn-warning btn-circle mr-3" title="Chnh sa">
                            <i className="fa fa-pencil-square" aria-hidden="true" />
                        </button>
                        <button onClick={this.onClickDelete} className="btn btn-danger btn-circle" title="Xa">
                            <i className="fa fa-trash" aria-hidden="true" />
                        </button>
                    </td>
                </tr>

                <Modal isOpen={this.state.isOpenModal} toggle={this.toggle}>
                    {this.state.isLoading ?  <Spin /> : null}       
                    <ModalHeader toggle={this.toggle}>CHINH SA THNG TIN KHACH HANG</ModalHeader>
                    <ModalBody>
                        <Form 
                            {...layout} 
                            initialValues={{
                                sex: this.props.customer.sex,
                                job: this.props.customer.job,
                                fullName: this.props.customer.fullName,
                                email: this.props.customer.email,
                                phoneNumber : this.props.customer.phoneNumber,
                                workPlace : this.props.customer.workPlace,
                                dateBirth : moment(this.props.customer.dateBirth),
                                note : this.props.customer.note
                            }} 
                            ref={this.formRef} onFinish={this.onFinish}>
                            <Form.Item
                                name="nameBlock"
                                label="Khu tr/cn h"
                            >
                                <strong>{this.props.block && this.props.block.nameBlock}</strong>
                            </Form.Item>
                            <Divider />
                            <Form.Item
                                {...formItemLayout}
                                name="fullName"
                                label="Tn khch hng:"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Hy nhp tn khch hng'
                                    }
                                ]}
                            >
                                <Input placeholder="Hy nhp tn khch hng" />
                            </Form.Item>
                            <Form.Item 
                                {...formItemLayout}
                                name="email"
                                label="Email" 
                                rules={[{type: 'email'}]}>
                                <Input placeholder="abc@gmail.com" />
                            </Form.Item>

                            <Row>
                                <Col span={24}>
                                    <Form.Item >
                                        <Form.Item 
                                            {...formItemLayout}
                                            name = 'phoneNumber'
                                            label="S din thoi"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Hy nhp s in thoi'
                                                }
                                            ]}
                                        >
                                            <Input placeholder= '0xxx' />
                                        </Form.Item>
                                        <Form.Item 
                                            name="dateBirth" 
                                            label="Ngy sinh" 
                                            {...formItemLayout}
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Hy chn ngy sinh'
                                                }
                                            ]}
                                        >
                                            <DatePicker />
                                        </Form.Item>
                                        <Form.Item 
                                            name="job" 
                                            label="Ngh nghip" 
                                            {...formItemLayout}
                                        >
                                            <Radio.Group onChange={this.onChange} value={this.state.job}>
                                                <Radio value={'Sinh vin'}>Sinh Vin</Radio>
                                                <Radio value={'Ngi i lm'}>Ngi i lm</Radio>
                                            </Radio.Group>
                                        </Form.Item>

                                        <Form.Item 
                                            {...formItemLayout}
                                            name = 'workPlace'
                                            label="Ni cng tc"
                                        >
                                            <Input />
                                        </Form.Item>

                                        <Form.Item 
                                            name="sex" 
                                            label="Gii tnh" 
                                            {...formItemLayout}
                                        >
                                            <Radio.Group onChange={this.onChange}>
                                                <Radio value={'Nam'}>Nam</Radio>
                                                <Radio value={'N'}>N</Radio>
                                                <Radio value={'Khc'}>Khc</Radio>
                                            </Radio.Group>
                                        </Form.Item>

                                        <Form.Item 
                                            {...formItemLayout}
                                            name = 'tempRegBool'
                                            label="ng k tm tr"
                                        >
                                            <Checkbox checked={this.state.tempReg} onChange={this.onChangeTempReg}>ng k</Checkbox>
                                        </Form.Item>

                                        <Form.Item 
                                            {...formItemLayout}
                                            name = 'note'
                                            label="Ghi ch"
                                        >
                                            <TextArea placeholder="textarea with clear icon" allowClear onChange={this.onChange} />
                                        </Form.Item>

                                    </Form.Item>
                                    
                                </Col>
                            </Row>

                            <Form.Item
                                wrapperCol={{
                                    md: {span: 8, offset: 16}
                                }}
                            >
                                <Button htmlType='submit' type='primary' >Cp nht</Button>{' '}
                                <Button  onClick={this.toggle}>Hy</Button>
                            </Form.Item>
                        </Form>
                    </ModalBody>
                </Modal>
                <Modal isOpen={this.state.isOpenDeleteModal} toggle={this.toggle}>
                    {this.state.isLoading ?  <Spin /> : null}  
                    <ModalHeader toggle={this.toggle}>THNG BO</ModalHeader>
                    <ModalBody>
                        Chc chn mun xa khach hang {this.props.customer.fullName} ?
                    </ModalBody>
                    <ModalFooter>
                        <Button type="primary" onClick={this.onDeleting}>Xa</Button>{' '}
                        <Button type="secondary" onClick={this.toggleDelete}>Hy</Button>
                    </ModalFooter>
                </Modal>
                <ToastContainer />
            </React.Fragment>
        );
    }
}
