import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {Form, Input, Button, Select} from 'antd';
import {Row, Col} from 'reactstrap';
import axios from 'axios';
import data from '../../assets/provincial.json';
import {toast, ToastContainer} from 'react-toastify';
import {withRouter} from 'react-router-dom';

const {Option} = Select;

const layout = {
    labelCol: {span: 8},
    wrapperCol: {span: 16}
};
const tailLayout = {
    wrapperCol: {offset: 20, span: 16}
};

class Register extends Component {
    
    pass = React.createRef();
    
    state = {
        sex: '',
        province: ''
    }

   onFinish = values => {
       if (values.pass !== values.confirm) {
           toast.error('Xc nhn mt khu khng ng');
           this.pass.current.focus();
       }
       else {
           this.Register(values);
       }

   };

    Register = async (account) => {
        const register = await axios({
            url: 'https://api-gateway.vmotel.me/go-lang/user/register',
            method: 'POST',
            data: {
                fullName: account.fullName,
                sex: account.sex,
                userName: account.userName,
                email: account.email,
                province: account.province,
                address: account.address,
                pass: account.pass,
                confirm: account.confirm,
                role: 1
            }
        });

        if (register) {
            if (register.data && register.data.data) {
                toast.success('ng k thnh cng');

                setTimeout(() => {
                    this.props.history.push('/dang-nhap');        
                }, 5000);
            }
            else if (register.data.message)
            {
                toast.error('Ti khon  tn ti !');
            }
        }
    }

   onGenderChange = value => {
       this.setState({
           sex : value.sex,
           province: value.province
       });
   }

   renderProvinces = () => {
       return data.map(item => {
           return <Option 
               key={item.province} 
               value={item.province}>{item.province}
           </Option>;
       });
   }

   render() {
       return (
           <div className="container">
               <div className="card o-hidden border-0 shadow-lg my-5">
                   <div className="card-body p-0">
                       {/* Nested Row within Card Body */}
                       <div className="row">
                           <div className="col-lg-4 d-none d-lg-block">
                               <img src= "https://r-cf.bstatic.com/images/hotel/max1024x768/148/148672944.jpg" alt= "" style = {{width: '100%', height: '100%'}} />
                           </div>
                           <div className="col-lg-8">
                               <div className="p-5">
                                   <div className="text-center">
                                       <h1 className="h4 text-gray-900 mb-4">ng k ti khon</h1>
                                   </div>
                                   <Form
                                       {...layout}
                                       name="basic"
                                       initialValues={{remember: true}}
                                       onFinish={this.onFinish}
                                   >
                                       <Row>
                                           <Col>
                                               <Form.Item
                                                   label="H tn"
                                                   name="fullName"
                                                   rules={[{required: true, message: 'Nhp h tn'}]}
                                               >
                                                   <Input />
                                               </Form.Item>
                                           </Col>

                                           <Col>
                                               <Form.Item
                                                   label="Gii tnh: "
                                                   name="sex"
                                                   rules={[{required: true, message: 'Phi chn gii tnh'}]}
                                               >
                                                   <Select
                                                       placeholder="Chn gii tnh"
                                                       onChange={this.onGenderChange}
                                                       allowClear
                                                   >
                                                       <Option value="male">Nam</Option>
                                                       <Option value="female">N</Option>
                                                   </Select>
                                               </Form.Item>
                                           </Col>
                                       </Row>
                                       
                                       <Row>
                                           <Col>
                                               <Form.Item
                                                   label="Ti khon"
                                                   name="userName"
                                                   rules={[{required: true, message: 'Hy nhp ti khon !'}]}
                                               >
                                                   <Input placeholder="Nhp ti khon" />
                                               </Form.Item>
                                           </Col>

                                           <Col>
                                               <Form.Item
                                                   label="Email: "
                                                   name="email"
                                                   rules={[{required: true, message: 'Hy nhp email', type: 'email'}]}
                                               >
                                                   <Input />
                                               </Form.Item>
                                           </Col>
                                       </Row>

                                       <Row>
                                           <Col>
                                               <Form.Item
                                                   label="Mt khu"
                                                   name="pass"
                                                   rules={[{required: true, message: 'Hy nhp mt khu'}]}
                                               >
                                                   <Input.Password ref={this.pass} />
                                               </Form.Item>
                                           </Col>
                                           <Col>
                                               <Form.Item
                                                   label="Xc nhn"
                                                   name="confirm"
                                                   rules={[{required: true, message: 'Xc nhn mt khu'}]}
                                               >
                                                   <Input.Password />
                                               </Form.Item>
                                           </Col>
                                       </Row>

                                       <Row>
                                           <Col>
                                               <Form.Item
                                                   label="Tnh"
                                                   name="province"
                                                   rules={[{required: true, message: 'Hy chn tnh !'}]}
                                               >
                                                   <Select
                                                       placeholder="Chn gii tnh"
                                                       onChange={this.onGenderChange}
                                                       allowClear
                                                   >
                                                       {this.renderProvinces()}
                                                   </Select>
                                               </Form.Item>
                                           </Col>

                                           <Col>
                                               <Form.Item
                                                   label="a ch: "
                                                   name="address"
                                                   rules={[{required: true, message: 'Hy nhp a ch'}]}
                                               >
                                                   <Input />
                                               </Form.Item>
                                           </Col>
                                       </Row>

                                       <Form.Item {...tailLayout}>
                                           <Button type="primary" htmlType="submit">
                                                ng k
                                           </Button>
                                       </Form.Item>
                                   </Form>
                                   <hr />
                                   <div className="text-center">
                                       <Link className="small" to="/dang-nhap">ng nhp</Link> 
                                   </div>
                               </div>
                           </div>
                       </div>
                   </div>
               </div>
               <ToastContainer />
           </div>
       );
   }
}

export default withRouter(Register);