import React, {Component} from 'react';
import {Link} from 'react-router-dom';

export default class LogoutModal extends Component {

    logOut = () => {
        localStorage.removeItem('token');
        localStorage.removeItem('user');
    }

    render() {
        return (
            <div className="modal fade" id="logoutModal" tabIndex={-1} role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLabel">Ready to Leave?</h5>
                            <button className="close" type="button" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true"></span>
                            </button>
                        </div>
                        <div className="modal-body">ng sut  thoi khi phin lm vic</div>
                        <div className="modal-footer">
                            <button className="btn btn-secondary" type="button" data-dismiss="modal">Hy</button>
                            <Link onClick={this.logOut} className="btn btn-primary" to="/dang-nhap">ng sut</Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
